    "text": "query EndpointDetailPageQuery(\n  $endpointId: UUID!\n  $tokenListOffset: Int!\n  $tokenListLimit: Int!\n  $autoScalingRules_endpointId: String!\n  $autoScalingRules_filter: String\n  $autoScalingRules_offset: Int\n  $autoScalingRules_order: String\n  $autoScalingRules_before: String\n  $autoScalingRules_after: String\n  $autoScalingRules_first: Int\n  $autoScalingRules_last: Int\n  $skipScalingRules: Boolean!\n) {\n  endpoint(endpoint_id: $endpointId) {\n    name\n    status\n    endpoint_id\n    image @deprecatedSince(version: \"23.09.9\")\n    image_object @since(version: \"23.09.9\") {\n      name\n      namespace @since(version: \"24.12.0\")\n      humanized_name\n      tag\n      registry\n      architecture\n      is_local\n      digest\n      resource_limits {\n        key\n        min\n        max\n      }\n      labels {\n        key\n        value\n      }\n      size_bytes\n      supported_accelerators\n      id\n    }\n    desired_session_count @deprecatedSince(version: \"24.12.0\")\n    replicas @since(version: \"24.12.0\")\n    url\n    open_to_public\n    errors {\n      session_id\n      ...InferenceSessionErrorModalFragment\n    }\n    retries\n    runtime_variant @since(version: \"24.03.5\") {\n      human_readable_name\n    }\n    model\n    model_mount_destiation @deprecatedSince(version: \"24.03.4\")\n    model_mount_destination @since(version: \"24.03.4\")\n    model_definition_path @since(version: \"24.03.4\")\n    extra_mounts @since(version: \"24.03.4\") {\n      row_id\n      name\n      id\n    }\n    environ\n    resource_group\n    resource_slots\n    resource_opts\n    routings {\n      routing_id\n      session\n      traffic_ratio\n      endpoint\n      status\n      id\n    }\n    created_user_email @since(version: \"23.09.8\")\n    ...EndpointOwnerInfoFragment\n    ...EndpointStatusTagFragment\n    ...ChatUIModalFragment\n    ...ServiceLauncherPageContentFragment\n    id\n  }\n  endpoint_token_list(offset: $tokenListOffset, limit: $tokenListLimit, endpoint_id: $endpointId) {\n    total_count\n    items {\n      id\n      token\n      endpoint_id\n      domain\n      project\n      session_owner\n      created_at\n      valid_until\n    }\n    ...ChatUIModalEndpointTokenListFragment\n  }\n  endpoint_auto_scaling_rules: endpoint_auto_scaling_rule_nodes(endpoint: $autoScalingRules_endpointId, filter: $autoScalingRules_filter, order: $autoScalingRules_order, offset: $autoScalingRules_offset, before: $autoScalingRules_before, after: $autoScalingRules_after, first: $autoScalingRules_first, last: $autoScalingRules_last) @skipOnClient(if: $skipScalingRules) {\n    pageInfo {\n      hasNextPage\n      hasPreviousPage\n    }\n    edges {\n      node {\n        id\n        endpoint\n        metric_name\n        metric_source\n        threshold\n        comparator\n        step_size\n        cooldown_seconds\n        min_replicas\n        max_replicas\n        created_at\n        last_triggered_at\n        ...AutoScalingRuleEditorModalFragment\n      }\n    }\n  }\n}\n\nfragment AutoScalingRuleEditorModalFragment on EndpointAutoScalingRuleNode {\n  id\n  endpoint\n  metric_name\n  metric_source\n  threshold\n  comparator\n  step_size\n  cooldown_seconds\n  min_replicas\n  max_replicas\n}\n\nfragment ChatUIModalEndpointTokenListFragment on EndpointTokenList {\n  items {\n    id\n    token\n    created_at\n    valid_until\n  }\n}\n\nfragment ChatUIModalFragment on Endpoint {\n  endpoint_id\n  url\n  status\n}\n\nfragment EndpointOwnerInfoFragment on Endpoint {\n  id\n  created_user_email @since(version: \"23.09.8\")\n  session_owner_email @since(version: \"23.09.8\")\n}\n\nfragment EndpointStatusTagFragment on Endpoint {\n  id\n  status\n}\n\nfragment InferenceSessionErrorModalFragment on InferenceSessionError {\n  session_id\n  errors {\n    repr\n  }\n}\n\nfragment ServiceLauncherPageContentFragment on Endpoint {\n  endpoint_id\n  desired_session_count @deprecatedSince(version: \"24.12.0\")\n  replicas @since(version: \"24.12.0\")\n  resource_group\n  resource_slots\n  resource_opts\n  cluster_mode\n  cluster_size\n  open_to_public\n  model\n  model_mount_destination @since(version: \"24.03.4\")\n  model_definition_path @since(version: \"24.03.4\")\n  environ\n  runtime_variant @since(version: \"24.03.5\") {\n    name\n    human_readable_name\n  }\n  extra_mounts @since(version: \"24.03.4\") {\n    row_id\n    id\n  }\n  image_object @since(version: \"23.09.9\") {\n    name @deprecatedSince(version: \"24.12.0\")\n    namespace @since(version: \"24.12.0\")\n    humanized_name\n    tag\n    registry\n    architecture\n    is_local\n    digest\n    resource_limits {\n      key\n      min\n      max\n    }\n    labels {\n      key\n      value\n    }\n    size_bytes\n    supported_accelerators\n    id\n  }\n  name\n}\n"
